import { Component, Input, OnInit } from '@angular/core';
import { ProjectsService, Project } from '../../../../services/projects.service';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.css']
})
export class DetailComponent implements OnInit {
  // @Input() filter: any = null;
  // currentProject: any = {};
  projectsToShow: Project[] = [];
  // tslint:disable-next-line:no-shadowed-variable
  animate = 'animated fadeInRightBig'; // animated fadeOutLeft

  // tslint:disable-next-line:variable-name
  constructor( private _projectService: ProjectsService ) { }

  ngOnInit() {
    // this.projectsToShow = this._projectService.getProjects();
  }

  showProjects( develop: any ) {
    /*
    Se creo la promesa ya que la variable projectsToShow cuando es llamada por el componente
    projects Angular la encuentra utilizando realizando un check, cuando finaliza se procede a modificarla
    */
    Promise.resolve(this.projectsToShow).then(() => {
      this.projectsToShow.forEach( project => {
        project.active = false;
      });

      if ( develop.id === 1 ) { // All project
        this.projectsToShow = this._projectService.getProjects();
      } else {
        this.projectsToShow = this._projectService.getProjectsByFilter(develop);
      }
      if ( this.projectsToShow.length > 0 ) {
        this.projectsToShow[0].active = true;
      }
    });
  }

  nextProject( nextProject: any ) {

    this.animate = 'animated fadeOutLeft';
    // tslint:disable-next-line:no-shadowed-variable
    setTimeout(() => {
      this.projectsToShow.forEach( project => {
        if ( project.active ) {
          project.active = false;
        }
      });
      nextProject.active = true;
      this.animate = 'animated fadeInRightBig';
    }, 700);

  }

}
