import { Component, AfterViewInit, OnInit, ElementRef, ViewChild, Renderer2 } from '@angular/core';
import { FiltersService, Filter } from '../../../services/filters.service';
import { DetailComponent } from './detail/detail.component';
// '../../../../services/projects.service'

@Component({
  selector: 'app-projects',
  templateUrl: './projects.component.html',
  styleUrls: ['./projects.component.css']
})
export class ProjectsComponent implements OnInit, AfterViewInit {
   @ViewChild(DetailComponent, {static: false}) detailProject: DetailComponent;
  // @ViewChild("menuHome", {static: false}) menuHome: ElementRef;
  filterActive: Filter = null;

  filters: Filter[] = [];
  // private renderer: Renderer2

  // tslint:disable-next-line:variable-name
  constructor( private _filterService: FiltersService ) { }

  ngOnInit() {
    this.filters = this._filterService.getFilters();
    this.filterActive = this.filters[0];
  }

  ngAfterViewInit() {
    this.detailProject.showProjects(this.filterActive);
  }

  activeFilter( filter: any ) {
    this.filterActive.active = false;
    filter.active = true;
    this.filterActive = filter;
  }

}
