export default "/*\n * Globals\n */\n\n\n/* Links */\n\n\na,\na:focus,\na:hover {\n    color: #fff;\n}\n\n\n/* Custom default button */\n\n\n.btn-default {\n    color: #fff;\n    text-shadow: none;\n    /* Prevent inheritence from `body` */\n    background-color: transparent;\n    border: 2px solid #fff;\n    border-radius: 20px;\n    padding: 0.5rem 2rem;\n}\n\n\n.btn-default:hover,\n.btn-default:focus {\n    background-color: rgba(255, 255, 255, 0.3);\n}\n\n\n/*\n * Base structure\n */\n\n\nhtml,\nbody {\n    height: 100%;\n}\n\n\n.image-background-page-blog {\n    background: url(/assets/img/background-working-blog.jpg) no-repeat center center fixed;\n    background-size: cover;\n    color: #fff;\n    text-align: center;\n    font-family: 'Quicksand', sans-serif;\n    font-size: 18px !important;\n    height: 100%;\n}\n\n\n.cover-copy {\n    color: #fff !important;\n}\n\n\n.btn-notify {\n    border: 2px solid #fff !important;\n}\n\n\n/* Extra markup and styles for table-esque vertical and horizontal centering */\n\n\n.site-wrapper {\n    display: table;\n    width: 100%;\n    height: 100%;\n    /* For at least Firefox */\n    min-height: 100%;\n    background: rgba(48, 53, 70, 0.5);\n    box-shadow: inset 0 0 100px rgba(0, 0, 0, 0.5);\n}\n\n\n.site-wrapper-inner {\n    display: table-cell;\n    vertical-align: top;\n}\n\n\n.cover-container {\n    margin-right: auto;\n    margin-left: auto;\n}\n\n\n/* Padding for spacing */\n\n\n.inner {\n    padding: 30px;\n}\n\n\n/*\n * Header\n */\n\n\n.masthead-brand {\n    margin-top: 10px;\n    margin-bottom: 10px;\n    color: #fff !important;\n}\n\n\n.nav-masthead {\n    text-align: center;\n    display: block;\n    color: #fff !important;\n}\n\n\n.nav-masthead .nav-link {\n    display: inline-block;\n    color: #fff !important;\n}\n\n\n@media (min-width: 768px) {\n    .masthead-brand {\n        float: left;\n    }\n    .nav-masthead {\n        float: right;\n    }\n}\n\n\n/*\n * Cover\n */\n\n\n.cover {\n    padding: 0 20px;\n}\n\n\n.cover .btn-notify {\n    padding: 10px 60px;\n    font-weight: 500;\n    text-transform: uppercase;\n    border-radius: 40px;\n}\n\n\n.cover-heading {\n    font-weight: 500;\n    text-transform: uppercase;\n    letter-spacing: 10px;\n    font-size: 2rem;\n    margin-bottom: 15px !important;\n}\n\n\n.sub-header {\n    font-size: 23px;\n    letter-spacing: 6px;\n    color: #fff !important;\n}\n\n\n.cover-heading {\n    color: #fff !important;\n}\n\n\n@media (min-width: 768px) {\n    .cover-heading {\n        font-size: 3.4rem;\n        letter-spacing: 15px;\n    }\n}\n\n\n.cover-copy {\n    max-width: 500px;\n    margin: 0 auto 3rem;\n}\n\n\n/*\n * Footer\n */\n\n\n.mastfoot {\n    color: #999;\n    /* IE8 proofing */\n    color: rgba(255, 255, 255, 0.5);\n}\n\n\n/*\n * Subscribe modal box\n */\n\n\n#subscribeModal .modal-content {\n    background-color: #303546;\n    color: #fff;\n    text-align: left;\n}\n\n\n#subscribeModal .modal-header,\n#subscribeModal .modal-footer {\n    border: 0;\n}\n\n\n#subscribeModal .close {\n    color: #fff;\n}\n\n\n#subscribeModal .form-control {\n    margin-top: 1rem;\n    background: rgba(0, 0, 0, 0.4);\n    color: #fff;\n}\n\n\n#subscribeModal .form-control:focus {\n    border-color: #49506a;\n}\n\n\n/*\n * Affix and center\n */\n\n\n@media (min-width: 768px) {\n    /* Pull out the header and footer */\n    .masthead {\n        position: fixed;\n        top: 0;\n    }\n    .mastfoot {\n        position: fixed;\n        bottom: 0;\n    }\n    /* Start the vertical centering */\n    .site-wrapper-inner {\n        vertical-align: middle;\n    }\n    /* Handle the widths */\n    .masthead,\n    .mastfoot,\n    .cover-container {\n        width: 100%;\n        /* Must be percentage or pixels for horizontal alignment */\n    }\n}\n\n\n@media (min-width: 992px) {\n    .masthead,\n    .mastfoot,\n    .cover-container {\n        width: 1060px;\n    }\n}\n/*# sourceMappingURL=data:application/json;base64, */"