import {Component, ElementRef, Input, OnInit, AfterContentInit, AfterViewInit, ViewChild} from '@angular/core';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-description-services',
  templateUrl: './description.component.html',
  styleUrls: ['./description.component.css']
})
export class DescriptionComponent implements OnInit, AfterContentInit, AfterViewInit {
  @ViewChild('contentDevelopment', {static: false}) modalDevelopment: ElementRef;
  @ViewChild('contentErs', {static: false}) modalErs: ElementRef;
  @ViewChild('contentArchitecture', {static: false}) modalArchitecture: ElementRef;
  @ViewChild('contentSupport', {static: false}) modalSupport: ElementRef;

  title: any = null;

  servicesCubi: any = {
    development: ElementRef,
    ers: ElementRef,
    architecture: ElementRef,
    support: ElementRef
  };

  // tslint:disable-next-line:ban-types
  constructor(private modalService: NgbModal) {
  }

  ngOnInit() {
  }

  ngAfterContentInit(): void {
    // throw new Error("Method not implemented.");
  }

  ngAfterViewInit(): void {
    this.servicesCubi.development = this.modalDevelopment;
    this.servicesCubi.ers = this.modalErs;
    this.servicesCubi.architecture = this.modalArchitecture;
    this.servicesCubi.support = this.modalSupport;
  }

  showModalService(name: string, size: string) {
    const modalRef = this.modalService.open(this.servicesCubi[name], {
        windowClass: 'modal-holder',
        centered: true,
        size,
      backdrop: 'static',
      keyboard : true
      });
  }

}
